$light-grey: #eee;

body {
  margin-right: 10px;
  margin-left: 10px;
  font-size: 14px;
  line-height: 1.4;
}

em {
  font-style: italic;
}

body,
h1,
h2,
h3 {
  font-weight: 300;
  margin-bottom: 1em;
}

h1 { font-size: 20px; }
h2 { font-size: 16px; margin-top: 1em; }

table,
progress {
  width: 100%;
}

th,
td,
[type=text],
textarea {
  margin-right: 5px;
  padding: 3px 6px;
}

th {
  width: 10%;
  font-weight: 500;
  text-align: right;
  white-space: nowrap;
  vertical-align: middle;
}

[type=text],
textarea {
  width: 200px;
  padding: 5px;
  border: 1px solid darken($light-grey, 20);
  border-radius: 3px;
  outline: 0;

  &:focus {
    border-color: darken($light-grey, 30);
    box-shadow: 0 0 5px $light-grey;
  }
}

textarea {
  height: 100px;
  font-family: monospace;
  vertical-align: bottom;
}

button {
  margin: 3px;
  padding: 6px 12px;
  border: 0;
  border-radius: 3px;
  outline: 0;
  background-color: $light-grey;

  &:focus {  background: darken($light-grey, 5); }
  &:hover {  background: darken($light-grey, 10); }
  &:active { background: darken($light-grey, 15); }
}


.my-slider {
  -webkit-appearance: none;
   border-radius:5px;
   /* transform: rotate(270deg); */
}

.my-slider::-webkit-slider-runnable-track {
 background-color: rgb(58, 55, 55);
 padding:0px;
 height: 10px;
}


.my-slider::-webkit-slider-thumb {
 -webkit-appearance: none;
  height: 20px;
  width: 20px;
  border-radius: 100%;
  background: pink;
  box-shadow: 2px 2px 1px #000000;
  margin-top: -5px;
}

body {
padding-top:20px;
text-align: center;
}

.container {
margin:20px auto;
height:auto;
}